<!-- src/views/Registration.vue -->
<template>
  <div class="registration-page">
    <div class="registration-form">
      <h2>Регистрация</h2>
      <form @submit.prevent="register">
        <div class="form-group">
          <label for="username">Имя пользователя</label>
          <input type="text" id="username" v-model="username" required />
        </div>
        <div class="form-group">
          <label for="email">Почта</label>
          <input type="email" id="email" v-model="email" required />
        </div>
        <div class="form-group">
          <label for="password">Пароль</label>
          <input type="password" id="password" v-model="password" required />
        </div>
        <div class="form-group">
          <label for="confirmPassword">Повторите пароль</label>
          <input type="password" id="confirmPassword" v-model="confirmPassword" required />
        </div>
        <div class="form-group">
          <p v-if="passwordsMatch" class="password-match">Пароли совпадают</p>
          <p v-else class="password-no-match">Пароли не совпадают</p>
        </div>
        <button type="submit" :disabled="!passwordsMatch">Зарегистрироваться</button>
      </form>
    </div>
    <Footer />
  </div>
</template>

<script>
import Footer from '../components/Footer.vue';

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Registration',
  components: {
    Footer
  },
  data() {
    return {
      username: '',
      email: '',
      password: '',
      confirmPassword: ''
    };
  },
  computed: {
    passwordsMatch() {
      return this.password === this.confirmPassword;
    }
  },
  methods: {
    register() {
      if (this.passwordsMatch) {
        // Логика регистрации пользователя
        alert('Регистрация прошла успешно!');
      }
    }
  }
};
</script>

<style scoped>
.registration-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #f5f5f5;
  padding-bottom: 50px; /* чтобы был отступ для футера */
}
.registration-form {
  background-color: #B5A688;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: 300px;
  text-align: center;
}
.form-group {
  margin-bottom: 15px;
}
label {
  display: block;
  margin-bottom: 5px;
}
input {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
}
.password-match {
  color: green;
}
.password-no-match {
  color: red;
}
button {
  background-color: #6D8444;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 4px;
}
button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}
</style>
